.Header {
  display: flex;
  align-items: center;
  padding: 15px 10px 5px;
  margin: 0;
  width: calc(100% - 20px);
  max-height: 60px;
  text-align: left;
  font-size: 18px;
  background-color: rgb(38, 38, 40);
  user-select: none;
  color: rgb(15, 220, 128);
  border-bottom: 3px solid rgb(102, 102, 102);
}

.HeaderTitle {
  margin-right: 5%;
  margin-bottom: 10px;
  font-weight: bold;
  color: rgb(15, 220, 128);
  user-select: none;
  border: 3px solid rgb(15, 220, 128);
  padding: 5px;
  transition: border-color 0.3s, color 0.3s;
}

.HeaderTitle:hover {
  color: rgb(108, 236, 178);
  border-color: rgb(108, 236, 178);
}

.HeaderTip {
  font-size: 12px;
  font-style: italic;
}
